* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.calendar {
  border-radius: 5px;
  flex-wrap: wrap;
  gap: 1px;
  width: 726px;
  margin: auto;
  display: flex;
}

.calendar__day {
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  transition: transform .5s;
  display: flex;
}

.calendar__day:before {
  content: "";
  font-family: Arial, sans-serif;
  font-size: 30px;
  display: inline-block;
}

.calendar__day:hover {
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

